import React, {Component} from "react";

class TOS extends Component {
    render() {
        return (
            <div className="terms-of-service">
                <li>1. Be Kind to Each other</li>
                <li>2. This is a space for People of Color to promote and educate</li>
                <li>3. Hate Speech will not be tolerated</li>
                <li>4. Only POC Streamers will be approved</li>
                <li>5. All Streams will need to go through the approval process</li>
                <li>6. All reports will be reviewed</li>
                <li>7. All Reports must be submitted with video evidence</li>
                <li>8. Moderation Team Applications can be sent to burn447.tv+socMod@gmail.com</li>
            </div>
        )
    }
}

export default TOS